<template>
	<view class="" style="display: flex;flex-flow: column nowrap;align-items: center;width: 750upx; ">
		<navigationview :current="current" :values="items" v-on:clickItem="onClickItem" :list_id="list_id"></navigationview>
		


		<scroll-view scroll-y class="jichukuangjia" :style="'height:'+height+'px'  " v-show="current === 0">
			<image class="" v-for="(item1,index1) in  data_list" :key="index1" :src="picview_before_title_loc+data_list[index1].pic_filepath"
			 style="width: 750upx; height: 500upx; flex-flow: column;z-index: 99;">
			</image>
		</scroll-view>

		<scroll-view scroll-y class="jichukuangjia" :style="'height:'+height+'px' " v-if="current === 1">
			<handleview  :card_id="luyouData[list_id].card_id"></handleview>
		</scroll-view>


	</view>
</template>

<script>
    import navigationview from '../../../components/navigation.vue';
	import handleview from '../../../components/handle-view.vue';

	export default {


		data() {
			return {
				// pic_before_title: "http://kaikaiomg-kaikaiomg.stor.sinaapp.com/uni_app_image/",
				luyouData: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
					{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
				],
				list_id: null,
				items: [
					'图示',
					'改进',
				],
				height: 0,
				width: 0,
				current: 0,
				data_list: [],
                picview_before_title_loc:'',
			}
		},
		components: {
			handleview,
			navigationview,
		},
		onLoad: async function(option) {

			console.log("list_id:" + option.list_id)
			this.picview_before_title_loc=this.picview_before_title;
			this.list_id = option.list_id;
			let aa = await this.get_hot_card_list()
			this.height = uni.getSystemInfoSync().windowHeight - 70 * (uni.getSystemInfoSync().windowWidth / 750);
			setTimeout(() => this.getData_list(), 500);
		},

		methods: {
			onClickItem(index) {
				if (this.current !== index) {
					this.current = index;
				}
			},

		
		
			

			gotonewitem: function() {
				uni.navigateTo({
					url: "../item_new/item_new?data=" + JSON.stringify(this.data_list[0]) + "&card_id=" + this.luyouData[this.list_id]
						.card_id,
				})
			},

			getData_list() {
				uni.request({
					url: 'https://kaikaiomg.applinzi.com/uni_app_download.php',
					method: "POST",
					data: {
						'form': this.luyouData[this.list_id].getdata_title,
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (ret) => {
						if (ret.statusCode !== 200) {
							console.log("请求失败:", ret)
						} else {
							console.log("下载数据:", ret.data)
							this.data_list = ret.data.data;
						}
					}
				});
			},

		}
	}
</script>

<style>
	.rank_title {
		line-height: 50upx;
		align-content: center;
		justify-content: center;
		font-size: 20upx;

	}

	.dark_bacgound {
		background-color: #ADD8E6;
	}

	.jichukuangjia {
		display: flex;
		/*   position: fixed;
				bottom: 0upx; */
		/* 		background-image: url('http://kaikaiomg-kaikaiomg.stor.sinaapp.com/uni_app_image/bc.jpg'); */
		background-color: white;
		width: 750upx;
		overflow: hidden;
		flex-flow: column nowrap;
		position: fixed;
		bottom: 0upx;
		/* justify-content: center; */
		/* align-items: center; */
	}
</style>



